.p {
  font-size: 40px;
  margin-bottom: 50px;
  margin-top: 100px;
  text-align: center;
}
.hero {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  max-width: 1600px;

  padding-top: 10px;
  padding-bottom: 10px;
}

.heroButton {
  width: 280px;
  height: 120px;
  font-size: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  background-position: center;
  padding: 0;
  margin-bottom: 50px;
  margin-left: 20px;
  box-shadow: 2px 5px 10px rgba(10, 32, 173, 0.85);
}

.heroButton:hover,
.heroButton:focus {
  color: rgb(7, 180, 65);
}

.heroLink {
  cursor: pointer;
  text-decoration: none;
  color: #023c02;
}

.heroLink:hover,
.heroLink:focus {
  color: rgb(7, 180, 65);
}

.icon {
  margin-left: 7px;
}
